	import {
		get,
		post,
		config,
		idcardValidate,
		getGiftspack
	} from '@/utils/api.js';

	import cityDatas from '../../components/city.area.js';
	import uniPopup from '../../components/uni-pop.vue';
	
	import QR from "@/components/wxqrcode.js" // 二维码生成器

	export default {

		components: {
			uniPopup
		},

		data() {
			name: 'prizeList'
			return {
				imgUrl: config.staticUrl,
				
				showHXqr:false,  //是否展示核销码
				ewmUrl: config.hxqrUrl, //生成的核销码的前缀
				hxqrImg:'',		 // 二维码
				
				yzmRes:'',// 验证码接口数据
				sec:0,
				yzval:'',
				
				currentPage:1, //未领取
				count:10, 
								
				unCurrentPage:1, //未领取
				unCount:10, 
				
				getCurrentPage:1, //已经领取
				getCount:10,
				
				subParams:{
					prizeVcode:'',
					infoKey:''
				},
				
				useStatus:0, //领取状态 0未领取、1已领取
				imgUrl: config.staticUrl,
				// 省市区地址
				provinceData: cityDatas,
				cityData: [],
				areaData: [],
				selectList: [{
					txt: '请选择'
				}, {
					txt: '请选择'
				}, {
					txt: '请选择'
				}],
				
				region:'', //所在地区
				tabOne: '请选择',
				indexTab: 0,
				proviceShow: true,
				areaShow: false,
				cityShow: false,
				show: false,
				type: '',
				checkOne: null,
				checkTwo: null,
				checkThree: null,
				// end

				seeInfo: false, // 回显form的信息
				subOk: false,
				showtjOK: false, // 提交成功
				showForm: false, // 填写表单
				userName: '',
				idcard:'',
				mobile: '',
				region: '',
				address: '',

				tabs: [{
						idx: 1,
						isActive: true,
						itemName: '未领取'
					},
					{
						idx: 2,
						isActive: false,
						itemName: '已领取'
					},
				],
				active: false,

				unGetShow: true,
				getShow: false,

				unGetPrize: [], //未领取列表
				getPrize: [], //已领取列表
				nuGetNext: true,
				getNext: true,

				start: 0,
				count: 20,
				isEnd: false,
				noData: false,
				isDisabled:false,
				regYzcode:/^[0-9]{4}$/ , //验证验证码
				regMobile:/^1[34578]\d{9}$/, //验证手机号码
				// regMobile:/^1[0-9]{10}$/; //验证手机号
				
				windowHeight:this.windowHeight
			}
		},
		onLoad() {
			this.queryPrize();
		},
		
		onShow(){
			if(this.useStatus == 0){  // 0未领取、1已领取
				uni.showModal({
					content: this.useStatus ,
				})
				this.isDisabled =  false
			} else if( this.useStatus == 1 ){
				uni.showModal({
					content: this.useStatus ,
				})
				this.isDisabled =  true
			}
		},
		async onReachBottom() {
			if (this.isEnd) return;
			uni.showLoading({
				title: '加载中...'
			})
			await this.onSearch(false); // 下一页
			uni.hideLoading();
		},
		methods: {
			// 查询中奖列表[未领取]
			async queryPrize(){
				uni.showLoading({
					title: '加载中...',
				})
				var params = {
					"openid": this.$store.state.openid, // 
					'hbopenid':'',
					"projectServerName": 'liaoning',
					"currentPage": this.currentPage,
					"count":this.count,
					"useStatus":this.useStatus //领取状态 0未领取、1已领取
				};
				this.prizeResult = await post('/gifts/queryPrizeList', params);
				console.log(this.prizeResult); // reply result replyTime
				uni.hideLoading();
				var jo = this.prizeResult;
				
				if (jo.result.businessCode == 0) {
					if(this.useStatus == 0){ // 0未领取
					   this.unGetPrize = jo.reply.prizeRecordAry //未领取列表
					   if (jo.reply.prizeRecordAry < this.count) {
					    	this.nuGetNext  = false
					   }
					}
					if(this.useStatus == 1){  //1已领取
					   this.getPrize = jo.reply.prizeRecordAry  //已领取列表
					    if (jo.reply.prizeRecordAry < this.count) {
					    		this.getNext = false
					    }
					}
				}
			},
			
			// 领取奖品
			gainPrize(prizeVcode,infoKey) {
				this.clearForm();
				this.subParams.prizeVcode = prizeVcode;
				this.subParams.infoKey = infoKey;
				this.showForm = true;
			},
			// 校验表单是否为空
            formValue(){
				console.log(this.userName);
				// this.userName === '' || this.userName.indexOf(' ') !== -1
				if (this.userName === '') {
				  uni.showModal({
					title: '提示',
					content: '请填写正确的姓名哦！~',
				  })
				  return     
				} 
				if (!(this.regMobile.test(this.mobile))) {
				  uni.showModal({
					title: '提示',
					content: '请填写正确的手机号！~',
				  })
				  return  // this.address === '' || this.address.indexOf(' ') !== -1
				}
				if (!idcardValidate(this.idcard)) {
				  uni.showModal({
					title: '提示',
					content: '请填写正确的身份证号哦！~',
				  })
				  return
				}
				if (this.address === '') {
				  uni.showModal({
					title: '提示',
					content: '请填写正确的详细地址哦！~',
				  })
				   return
				}
				if (!(this.regYzcode.test(this.yzval))) {
				  uni.showModal({
					title: '温馨提示',
					content: '请填写正确的验证码哦！~',
				  })
				  return false
				} else {
					for(var x in this.selectList){
					   if(this.selectList[x].txt == '请选择') {//x为数组的下标
					      uni.showModal({
							title: '提示',
							content: '你还没有选择完整的所在地区信息哦~',
					      })
						  return false
					   } 
					}
				}
				this.sub_message();
			},
			// 提交表单
			// subForm() {
			// 	// this.formValue();  // 校验表单
			// 	// this.showForm = false;  //表单不显示
			// 	// this.showtjOK = true;   //提交成功显示
			// },
		
			clearForm(){
				this.userName = '';
				this.idcard = '';
				this.mobile = '';
				this.region = '';
				this.address = '';
				this.yzval = '';
				this.seeInfo = false;
			},
			async sub_message() {
				uni.showLoading({
					title: '提交中...',
				})
				var params = {
					"openid": this.$store.state.openid, // 
					"username": this.userName,
					"address":this.region + this.address.trim(),
					"idcard": this.idcard,//'idcard'
					"phonenum":this.mobile,
					"prizeVcode":this.subParams.prizeVcode,
					"prizeInfoKey":this.subParams.infoKey,
					"captcha":this.yzval,
					"projectServerName": 'liaoning',
				};
				this.tjResult = await post('/user/savePrize', params);
				console.log(this.tjResult); // reply result replyTime
				uni.hideLoading();
				var jo = this.tjResult;
				
				if (jo.result.businessCode == 0) {
					 this.showtjOK = true;
					  setTimeout(() => {
						this.showForm = false;
						this.queryPrize();   // 查询大奖列表
					  }, 2000)
					  this.clearForm();
				} else {
				  uni.showModal({
					title: '温馨提示',
					content: jo.result.msg,
				  })
				}
			 },
			// 成功后我知道了
			iknow() {
				this.showForm = false;
				this.showtjOK = false;
				this.useStatus = 1;
				// 去往已领取tab面
				this.tabSwitch(1);
				this.queryPrize()   // 查询已领取列表
				// ...
			},
			
			// 生成核销二维码 加上前缀啊 hxqrUrl + ‘vcode’
			
			// 已领奖信息 查看form提交信息
			seeForm(idx) {
				this.showHXqr = false;  //是否展示核销码
				this.showForm = true;
				this.seeInfo = true;
				console.log(idx);
				console.log(this.getPrize)
				for(var k in this.getPrize){
					if(k == idx){
						console.log(k);
						if(this.getPrize[k].exchangeChannel == '8'){
							this.showHXqr = true; //展示核销码
							// 二维码
							var qrval = this.ewmUrl + this.getPrize[k].prizeVcode;
							let img = QR.createQrCodeImg(qrval, {
								size: parseInt(100)//二维码大小  
							})
							console.log(qrval)
							this.hxqrImg = img;
						} else {
							this.showHXqr = false;  //是否展示核销码
						}
						
						this.userName = this.getPrize[k].userName;
						this.idcard = this.getPrize[k].idCard;
						this.mobile = this.getPrize[k].phoneNum;
						
						console.log(this.getPrize[k].address)
						if(this.getPrize[k].address == undefined){
							this.region = '';
							this.address = '';
						} else {
							this.region = this.getPrize[k].address.split('-')[0];
							this.address = this.getPrize[k].address.split('-')[1];
						}
						this.isDisabled = true; //禁止输入
					}
				}
			},
			// 关闭按钮
			formClose(){
				this.showForm = false;
			},
			// 关闭已领奖信息form
			seeOk() {
				this.showForm = false;
			},
			//切换选项卡
			tabSwitch(tabIndex) {

				for (var i = 0; i < this.tabs.length; i++) {
					this.tabs[i].isActive = false;
				}
				this.tabs[tabIndex].isActive = true;
				if (tabIndex == 0) {
					this.unGetShow = true;
					this.getShow = false;
					
				    // 未领取
					this.currentPage = this.unCurrentPage;
					this.count = this.unCount;
					this.useStatus = tabIndex; // 未领取 0
					this.isDisabled =  false
					this.queryPrize()    // 查询未领取列表
				}
				if (tabIndex == 1) {
					this.getShow = true;
					this.unGetShow = false;
					
					// 已领取
					this.currentPage = this.getCurrentPage;
					this.count = this.getCount;
					this.useStatus = tabIndex; // 1 已领取
					this.isDisabled =  true
					this.queryPrize()   // 查询已领取列表
				}
			},
			// 获取验证码
			async getCaptcha() {
				uni.showLoading({
					title: '获取中...',
				})
				var params = {
					"phonenum": this.mobile,
					"projectServerName": 'liaoning',
				};
				this.yzmRes = await post('/user/getCaptcha', params);
				console.log('getCaptcha' + this.yzmRes); // reply result replyTime
				uni.hideLoading();
				var jo = this.yzmRes;
				if (jo.result.businessCode == 0) {
				  this.sec = 60;
				  this.timer();
				} else {
				  uni.showModal({
				    title: '提示',
				    content: jo.result.msg,
				  })
				}
			},
			
			getyz() {
			    if (this.sec > 0) {
			      return false;
			    }
			    if (!(this.regMobile.test(this.mobile))) {
			      uni.showModal({
			        title: '提示',
			        content: '请填写正确的手机号！~'
			      })
			    } else {
					this.getCaptcha();
					this.sec = 0;
			    }
			},
			timer() {
				let timer = null;
				if (this.stop || this.sec <= 0) {
				  this.sec = 0;
				  return false;
				}
				this.sec = this.sec - 1;
				timer = setTimeout(() => {
				  this.timer()
				}, 1000);
			},
			//下拉未领取
			async nextUnPrize(e) { 
				if (this.nuGetNext) {
				  uni.showLoading({
					title: '加载中',
					mask: true
				  })
				  this.currentPage = this.unCurrentPage + 1;
				  this.count = this.unCount;
				  // this.queryPrize();
				  uni.showLoading({
				  	title: '加载中...',
				  })
				  var params = {
				  	"openid": this.$store.state.openid, // this.$store.state.openid
					"hbopenid":'',
					"projectServerName": 'liaoning',
				  	"currentPage": this.currentPage,
				  	"count":this.count,
				  	"useStatus":this.useStatus //领取状态 0未领取、1已领取
				  };
				  this.prizeResult = await post('/gifts/queryPrizeList', params);
				  uni.hideLoading();
				  if(this.prizeResult.reply.prizeRecordAry.length>0){
					   if(this.useStatus = 0){
						   this.unGetPrize = this.unGetPrize.concat(this.prizeResult.reply.prizeRecordAry); //未领取列表
					   }else{
						   this.getPrize = this.getPrize.concat(this.prizeResult.reply.prizeRecordAry); //已领取列表
					   }
				   }
				}
			},
			//下拉已领取
			async nextGetPrize(e) {
				if (this.getNext) {
				  uni.showLoading({
					title: '加载中',
					mask: true
				  })
				  this.currentPage = this.getCurrentPage + 1;
				  this.count = this.getCount;
				  // this.queryPrize();
				  uni.showLoading({
				  	title: '加载中...',
				  })
				  var params = {
				  	"openid": this.$store.state.openid, // this.$store.state.openid
					"hbopenid":'',
					"projectServerName": 'liaoning',
				  	"currentPage": this.currentPage,
				  	"count":this.count,
				  	"useStatus":this.useStatus //领取状态 0未领取、1已领取
				  };
				  this.prizeResult = await post('/gifts/queryPrizeList', params);
				  uni.hideLoading();
				  if(this.prizeResult.reply.prizeRecordAry.length>0){
					   if(this.useStatus = 0){
						   this.unGetPrize = this.unGetPrize.concat(this.prizeResult.reply.prizeRecordAry); //未领取列表
					   }else{
						   this.getPrize = this.getPrize.concat(this.prizeResult.reply.prizeRecordAry); //已领取列表
					   }
				   }
				}
			},

			// 省市区地址
			togglePopup(type, open) {
				if(this.isDisabled == true){
					return  //不可以点击
				} else {
					this.type = type
					if (open === 'tip') {
						this.show = true
					} else {
						this.$refs[open].open()
					}
				}				
			},
			cancel(type) {  //取消地选择框
				if (type === 'tip') {
					// 选择地区框消失
					this.show = false
					return		
				}
				this.$refs[type].close();
				console.log(this.selectList);
				// 校验是否没有进行选择
				for(var x in this.selectList){
					console.log('aaa')
				   if(this.selectList[x].txt == '请选择') {//x为数组的下标
				      console.log('bbb');
				      uni.showModal({
						title: '提示',
						content: '你还没有选择完整的所在地区信息哦~',
				      })
					  return
				   } else{
					   this.region = this.selectList[0].txt+this.selectList[1].txt + this.selectList[2].txt + '-';
					   console.log(this.region);
					   // console.log(this.selectList[0].txt)
					   // 选择地区框消失
					   this.show = false
					   return
				   }
				}
			},
			change(e) {
				if (e.show == true) {
					uni.hideTabBar()
				} else {
					uni.showTabBar()
				}
			},
			tabEvent(index) {
				this.indexTab = index
				if (this.indexTab == 0) {
					this.proviceShow = true
					this.cityShow = false
					this.areaShow = false
					// this.checkOne = null
					this.checkTwo = null
					this.checkThree = null
					// this.cityData = []
					this.areaData = []
					// this.selectList[0].txt = "请选择"
					this.selectList[1].txt = "请选择"
					this.selectList[2].txt = "请选择"
				} else if (this.indexTab == 1) {
					this.proviceShow = false
					this.cityShow = true
					this.areaShow = false
					// this.checkTwo = null
					this.checkThree = null
					// this.areaData = []
					// this.selectList[1].txt = "请选择"
					this.selectList[2].txt = "请选择"
				} else if (this.indexTab == 2) {
					this.proviceShow = false
					this.cityShow = false
					this.areaShow = true
				}
			},
			provinceEvent(data, index) {
				this.checkOne = index
				this.selectList[0].txt = data.name
				this.indexTab = 1
				this.proviceShow = false
				this.cityShow = true
				this.areaShow = false
				this.cityData = data.cityList
			},
			cityEvent(data, index) {
				this.checkTwo = index
				this.selectList[1].txt = data.name
				this.indexTab = 2
				this.proviceShow = false
				this.cityShow = false
				this.areaShow = true
				this.areaData = data.areaList
			},
			areaEvent(data, index) {
				this.checkThree = index
				this.selectList[2].txt = data.name
			},
		}
	}
